body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.InfoIcon {
    background: transparent;
    padding: 0;
    margin-left: 5px;
    border: 0;
    font-size: 1.1em;
    color: darkgray;
    cursor: pointer;
    outline: none;
}

.InfoIcon:hover,
.InfoIcon:active {
    color: gray;
}
.Slider {
    width: 215px;
    padding: 25px 15px;
}

.Slider__label {
    padding-bottom: 8px;
}

.Slider__label:after {
    display: block;
    clear: both;
    content: '';
}

.Slider__name {
    float: left;
    width: 58%;
    font-weight: 500;
    color: #444444;
}

.Slider__value {
    float: left;
    width: 42%;
    text-align: right;
    font-weight: 700;
    color: red;
}

.Slider__value,
.Slider__name {
    line-height: 1.7em;
}

.Slider__unit {
    font-weight: 400;
    color: #888888;
}

.input-range__label--value {
    display: none;
}

.input-range__track--active {
    background: #dd2d2d;
}

.input-range__slider {
    background: #dd2d2d;
    border: 1px solid #dd2d2d;
}

.input-range__label--max .input-range__label-container,
.input-range__label--min .input-range__label-container {
    left: 0;
}

.input-range__label--min,
.input-range__label--max {
    bottom: -1rem;
}
.InfoBox {
    padding-top: 30px;
}

.InfoBox__name {
    display: block;
    font-size: 1.1em;
}

.InfoBox__value {
    font-size: 2.5em;
    color: green;
}

.InfoBox .warning {
    color: red;
}

.InfoBox__unit {
    font-size: 1.1em;
    color: gray;
    padding-left: 5px;
}
.Table {
    display: table;
    table-layout: fixed;
    margin: 0 0 40px 0;
    width: 100%;
    min-width: 800px;
    border-collapse: collapse;
    border-style: hidden;
}

.Table th {
    text-align: left;
    background: #eeeeee;
}

.Table td {
    border-top: 1px solid #dedede;
}

.Table td,
.Table th {
    padding: 18px 8px;
    display: table-cell;
}
.ComparisonTable th:first-child {
    width: 22%;
}

@media(max-width: 840px) {
    .ComparisonTable {
        min-width: auto;
    }

    .ComparisonTable,
    .ComparisonTable thead,
    .ComparisonTable tbody,
    .ComparisonTable tr {
        display: flex;
        flex-shrink: 0;
        flex-grow: 1;
    }

    .ComparisonTable tr {
        flex-direction: column;
    }

    .ComparisonTable th,
    .ComparisonTable td {
        display: block;
        white-space: nowrap;
        border-top: 1px solid #dedede;
        background: #ffffff;
        font-weight: 400;
    }

    .ComparisonTable th:first-child,
    .ComparisonTable td:first-child {
        width: auto;
        text-align: left;
        background: #eeeeee;
        font-weight: 700;
    }
}
.CloseIcon {
    background: transparent;
    padding: 0;
    margin: 0;
    border: 0;
    font-size: 2em;
    color: black;
    cursor: pointer;
    outline: none;
}

.CloseIcon:hover,
.CloseIcon:active {
    color: gray;
}
.Modal {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.Modal__modal {
    position: fixed;
    top: 220px;
    left: 50%;
    width: 700px;
    max-height: calc(100vh - 440px);
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    overflow-y: auto;
    background: white;
    border-radius: 8px;
}

.Modal__scrollable {
    bottom: 15px;
    width: 100%;
    max-height: 100%;
    overflow: scroll;
}

.Modal__content {
    padding: 0 20px;
}

.Modal__close {
    position: absolute;
    top: 10px;
    right: 10px;
}
.App {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

.title {
  max-width: 320px;
}

.title h1 {
  font-size: 2.2em;
  padding: 20px;
  text-align: right;
}

.colored {
  color: green;
}

.lead p {
  margin-top: 50px;
  padding: 24px 12px;
  border-left: 1px solid gray;
  max-width: 500px;
}

.Sliders {
  max-width: 490px;
}

.Results {
  padding-top: 30px;
}

.Buttons {
  padding: 20px 15px;
  text-align: right;
}

.Buttons button {
  outline: none;
  cursor: pointer;
}

.DetailsToggle {
  font-size: 0.9em;
  border: 0;
  padding-right: 15px;
  color: #999;
}

.DetailsToggle:hover,
.DetailsToggle:active {
  color: #666;
}

.ResetButton {
  color: white;
  font-size: 1.15em;
  background: #10a2d3;
  padding: 10px;
  border-radius: 6px;
  border: 0;
}

.ResetButton:active,
.ResetButton:hover {
  background: #057da5;
}

.Footer {
  margin: 20px auto 0;
  border-top: 1px solid rgb(200, 200, 200);
  padding: 60px 0;
  text-align: center;
}
.ExternalitiesTable th:first-child {
    width: 55%;
}

.ExternalitiesTable th,
.ExternalitiesTable td {
    white-space: normal;
}

@media(max-width: 840px) {
    .ExternalitiesTable {
        min-width: auto;
    }

    .ExternalitiesTable th:first-child {
        width: 35%;
    }
}
